import './Projects.css';
import { AnimatedSection } from './AnimatedSection';
import { motion } from 'framer-motion';
import backEndEcomerce from '../assets/backEndE-comerce.png';
import campanhaMarketing from '../assets/campanhaMarketingDigital.png';
import pizzaria from '../assets/Pizzaria.png';
import financas from '../assets/financas.png';
import gestorEstoque from '../assets/gestorDeEstoque.png';

export function Projects() {
  const projects = [
    {
      title: 'E-commerce Backend',
      description:
        'Backend para e-commerce com NestJS, Prisma e PostgreSQL. Sistema completo com autenticao JWT, gesto de produtos, carrinho, pagamentos via Stripe e rea administrativa.',
      image: backEndEcomerce,
      technologies: ['NestJS', 'TypeScript', 'Prisma', 'PostgreSQL', 'JWT'],
      github: 'https://github.com/Albanezfxp/E-comerce_backEnd',
      live: '#',
    },
    {
      title: 'Gerencimaneto de campanhas de marketing digital',
      description:
        'Sistema de gerenciamento de campanhas de marketing digital desenvolvido com Prisma, PostgreSQL e Zod. Implementa validao de dados, modelagem relacional e anlise de mtricas em tempo real. Permite gesto completa do ciclo de campanhas, desde planejamento at resultados.',
      image: campanhaMarketing,
      technologies: ['Node.js', 'PostgreSQL', 'Express', 'React'],
      github: 'https://github.com/Albanezfxp/Api_de_gerenciamento_De_Leads',
      live: '#',
    },
    {
      title: 'Pizzaria Mil Graus',
      description:
        'Landing page para pizzaria com cardpio digital, localizao no mapa, promoes especiais e informaes de contato.',
      image: pizzaria,
      technologies: ['React', 'CSS'],
      github: 'https://github.com/Albanezfxp/pizzaria_mil_graus',
      live: '#',
    },
    {
      title: 'Finanas Pessoais',
      description:
        'Aplicao para controle de finanas pessoais com API simulada usando json-server. Permite gerenciar receitas, despesas e acompanhar o oramento.',
      image: financas,
      technologies: ['JavaScript', 'HTML', 'CSS', 'JSON Server'],
      github: 'https://github.com/Albanezfxp/Finan-as-Pessoais',
      live: '#',
    },
    {
      title: 'Gestor de Estoque',
      description:
        'Sistema de gesto de estoque desenvolvido com React e TypeScript. Oferece controle de produtos, movimentaes e relatrios gerenciais.',
      image: gestorEstoque,
      technologies: ['React', 'TypeScript', 'CSS', 'Vite'],
      github: 'https://github.com/Albanezfxp/Gestor-de-Estoque',
      live: '#',
    },
  ];

  return (
    <AnimatedSection className="projects" id="projects">
      <div className="projects-content">
        <AnimatedSection direction="down">
          <h2>Projetos</h2>
        </AnimatedSection>

        <div className="projects-grid">
          {projects.map((project, index) => (
            <AnimatedSection
              key={project.title}
              direction="up"
              delay={index * 0.2}
              className="project-card"
            >
              <motion.div whileHover={{ scale: 1.02 }} transition={{ duration: 0.3 }}>
                <img src={project.image} alt={project.title} className="project-image" />
                <div className="project-info">
                  <h3>{project.title}</h3>
                  <p>{project.description}</p>
                  <div className="technologies">
                    {project.technologies.map((tech) => (
                      <span key={tech} className="tech-tag">
                        {tech}
                      </span>
                    ))}
                  </div>
                  <div className="project-links">
                    <a href={project.github} target="_blank" rel="noopener noreferrer">
                      GitHub
                    </a>
                    {project.live !== '#' && (
                      <a href={project.live} target="_blank" rel="noopener noreferrer">
                        Demo
                      </a>
                    )}
                  </div>
                </div>
              </motion.div>
            </AnimatedSection>
          ))}
        </div>
      </div>
    </AnimatedSection>
  );
}
